/**
 * This CSS ensures that Emoji are the same size as the font and are centered up correctly.
 */

.emoji {
  display: inline-flex;
  height: 1em;
  width: 1em;
  margin: 0 0.05em 0 0.1em;
  vertical-align: -0.14em;
  background: no-repeat center center;
  background-size: 1em 1em;
}

/**
 * This CSS handles the textcomplete boxes for selection.
 */

.textcomplete-item:hover {
  background-color: #6eb7db;
}

.textcomplete-item.active {
  background-color: #6eb7db;
}

.textcomplete-dropdown {
  opacity: 100 !important;
  transform: scale(1) !important;
}

/**
 * The following CSS is for proper rendering of icons in Bootstrap
 */

.alert.mdi::before,
.breadcrumb .mdi::before,
.btn.mdi::before,
.card-title.mdi::before,
.card-subtitle.mdi::before,
.card-link.mdi::before,
.dropdown-item.mdi::before,
.list-group-item.mdi::before,
.nav-link.mdi::before {
  font-size: 1.25em;
  line-height: initial;
  position: relative;
  top: 0.09rem;
}

.alert.mdi::before,
.breadcrumb .mdi:not(:empty)::before,
.btn.mdi:not(:empty)::before,
.card-title.mdi:not(:empty)::before,
.card-subtitle.mdi:not(:empty)::before,
.card-link.mdi:not(:empty)::before,
.dropdown-item.mdi:not(:empty)::before,
.nav-link.mdi:not(:empty)::before {
  margin-right: 0.25rem;
}

.list-group-item.mdi:not(:empty)::before {
  margin-right: 0.5rem;
}

.dropdown-item.mdi:not(:empty)::before {
  margin-left: -0.75rem;
}

.alert.mdi::before,
.list-group-item.mdi:not(:empty)::before {
  margin-left: -0.5rem;
}

.modal-title.mdi::before {
  font-size: 1.5em;
  line-height: 0.5;
  position: relative;
  top: 0.26rem;
  margin-right: 0.5rem;
}

.footer {
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 60px;
  line-height: 60px;
  background-color: #f5f5f5;
}

